*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  list-style: none;
  color: #333;
}

p{
  color: #666;
}

a{
  text-decoration: none;
}

input{
  border: none;
}

input:focus{
  outline: none;
}

nav{
  background-color: #333;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 9999;
}

.container{
  width: 1200px;
  margin: 0 auto;
}

nav .container{
  padding: 10px;
  display: flex;
  align-items: center;
}

nav > .container > .logo{
  object-fit: contain;
  flex-grow: 1;
}

nav > .container > .logo img{
  width: 50px;
}

nav > .container > .menu {
  display: flex;
  flex-grow: 9;
}

nav > .container > .menu li{
  margin-right: 50px;
  color: #fff;
}

.App {
  padding: 150px 0 150px 0;
}

.search-area {
  text-align: center;
  margin-bottom: 100px;
}

.search-area h1{
  margin-bottom: 10px;
}

.search-area form{
 padding-top: 50px;
}

.search-area input{
  border-bottom: 1px solid #333;
  width: 30%;
  font-size: 1.5rem;
  padding: 10px 0px 10px 5px;
  transition: all 0.15s ease-in-out;
}

.search-area .search-logo{
  transform: translateX(-30px);
  color: #aaa;
  transition: all 0.15s ease-in-out;
}

.search-area input:focus{
  width: 40%;
  border-bottom: 1px solid #61dafb;
}

.search-area input:focus + .search-logo{
  color: #61dafb;
}

.movie-area {
  margin-bottom: 50px;
}

.movie-area .movie-list{
  display: flex;
  flex-wrap: wrap;
}

.movie-area .movie-list .movie-wrap{
  width: 25%;
  padding: 20px;
}

.movie-area .movie-list .movie-wrap img{
  width: 100%;
}
.movie-area .movie-list .movie-wrap .movie-content{
  background-color: #f5f5f5;
  padding: 10px;
}

.movie-area .movie-list .movie-wrap h4{
  height: 40px;
  line-height: 1;
}

.movie-area .movie-list .movie-wrap h3{
  color: #db6a36;
}

.movie-area .movie-list .movie-wrap .detail-btn{
  background-color: #282c34;
  padding: 15px 15px 15px 10px ;
  display: flex;
  align-items: center;
}

.movie-area .movie-list .movie-wrap .detail-btn a{
  color: #61dafb;
  text-transform: uppercase;
  margin-right: 10px;
  transition: all 0.15s ease-in-out;
}

.movie-area .movie-list .movie-wrap .detail-btn i{
  color: #888;
  font-size: 12px;
}

.movie-area .movie-list .movie-wrap .detail-btn a:hover{
  margin-right: 15px;
}

.movie-area .movie-list .movie-wrap .detail-btn a:hover + i{
  color: #61dafb;
}

.movie-infowrap{
  display: flex;
  position: relative;
  padding-bottom: 60px;
}

.movie-infowrap .img{
  width: 40%;
}

.movie-infowrap .img img{
  width: 100%;
}

.movie-infowrap .txt {
  width: 60%;
  padding: 15px 0 0 30px;
}

.movie-infowrap .txt h2 {
  margin-bottom: 5px;
}

.movie-infowrap .txt h4 {
  margin-bottom: 30px;
  color: #aaa;
}

.movie-infowrap .txt h2.rate {
  margin-bottom: 30px;
  color: #db6a36;
}

.movie-infowrap .txt p{
  width: 60%;
}

.movie-infowrap .close-btn{
  position: absolute;
  top: 0;
  right: 0;
  font-size: 24px;
}

.pagenation-wrap{
  text-align: center;
}

.pagenation {
  margin: 0 auto;
  display: flex;
  width: 300px;
}

.pagenation .wave-effect{
  flex-grow: 1;
  padding: 5px;
  border: 1px solid #f0f0f0;
}

.pagenation .wave-effect a{
  font-size: 18px;
  color: #888;
}

.pagenation .wave-effect.active{
  background-color: #61dafb;
  border: 1px solid #61dafb;
}

.pagenation .wave-effect.active a{
  color: #333;
}

.noMovie {
    text-align: center;
    width: 100%;
    height: 50vh;
    font-size: 24px;
    background-size: 40%;
    background-position: center 30px;
    background-repeat: no-repeat;
  background-image: url(./nomovie.png);
}

.noMovie p{
  color:#aaa;
}